<template>
  <div class="BaseInfo">
    <div class="title"></div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="clientName"
        label-width="50px"
        required
        name="clientName"
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' },{validator:validatorName,message: ''}]"
      />
      <van-field name="sex" required label="" label-width="50px">
        <template #input>
          <van-radio-group v-model="sex" direction="horizontal">
            <van-radio name="1"></van-radio>
            <van-radio name="2"></van-radio>
          </van-radio-group>
        </template>
      </van-field>
      <van-field
        v-model="age"
        label-width="50px"
        name="age"
        required
        type="number"
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' },{validator,message: '1870'}]"
      />
      <van-field
        is-link
        required
        readonly
        @click="show = true"
        v-model="city"
        label-width="50px"
        name="city"
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
      />
      <van-field
        v-model="phone"
        label-width="83px"
        name="phone"
        required
        type="number"
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' },{ pattern, message: '' }]"
      />
      <!-- <van-field
        v-model="smsCode"
        label-width="80px"
        name="smsCode"
        maxlength="6"
        required
        type="tel"
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
      >
        <template #button>
          <van-button size="small" type="info" native-type="button" @click="sendMsg">
            
          </van-button>
        </template>
      </van-field> -->
      <van-field
        v-model="loanAmount"
        label-width="83px"
        required
        type="number"
        name="loanAmount"
        label=""
        placeholder="()"
        :rules="[{ required: true, message: '' },{validator:validatorMount,message: '5000500'}]"
      />
      <div style="margin: 26px 16px 16px 16px;">
        <van-button round block type="info"></van-button>
      </div>
    </van-form>
    <van-popup v-model="show" position="bottom" :style="{ height: '46%' }">
        <van-area @cancel="cancelFn" :area-list="areaList" @confirm="confirmFn"
          columns-num="2" value="510100" title="" />
    </van-popup>
    <div class="tips">
      <!-- <div></div> -->
        <div></div>
        <div></div>
        <div></div>
    </div>
    <div class="footer">
      <div>  </div>
      <div>ICP2020030967-1</div>
    </div>
  </div>
</template>
<script>
import { Button,Popup,Area,RadioGroup, Radio, Toast } from "vant";
import axios from 'axios'
const areaList = require('../assets/area.json')
export default {
  name: 'BaseInfo',
  components: {
    vanButton: Button,
    vanPopup: Popup,
    vanArea: Area,
    vanRadioGroup:RadioGroup,
    vanRadio:Radio
  },
  data() {
    return {
        clientName: '',
        sex:'1',
        age:'',
        city:'',
        phone: '',
        smsCode:'',
        loanAmount:'',
        show: false,
        areaList:areaList,
        pattern: /^[1][3,4,5,6,7,89][0-9]{9}$/,
        // patternName:/^[\u4e00-\u9fa5]+$/ig,
        uid:'',
        sign:'',
        relCity:''
    }
  },
  mounted(){
    this.getSign()
  },
  methods:{
    validatorName(val){
      var reg = /^[\u4e00-\u9fa5]+$/ig
      return reg.test(val)
    },
    validator(val) {
      return val>=18 && val<=70;
    },
    validatorMount(val){
      return val>=5000 && val<=5000000;
    },
    onSubmit(values) {
      console.log('submit', values);
      values.city=this.relCity
      this.$router.push({
        path:'/AssetInfo',
        query:{
          ...this.$route.query,
          ...values,
          sign:this.sign
        }
      })
    },
    toSelect() {
        this.show = true;
    },
    confirmFn(value) {
        this.show = false;
        let str =''
        value.forEach(item=>{
          str += item.name+' '
        })
        this.city = str.slice(0,-1)
        this.relCity = value[1].name
        console.log(value)
    },
    cancelFn() {
        this.show = false;
    },
    getSign(){
      if(this.uid==''){
        this.uid = Date.parse(new Date())
      }
      axios.get(`https://web.rongxinvip.com/weixin/getAppSign?code=${this.uid}`)
      .then(res=>{
        if(res.data.code===0){
          this.sign = res.data.data
        }
        console.log('res',res)
      })
    },
    sendMsg(){
      if(this.phone===''){
        Toast({
          message: '',
          position: 'top',
        })
        return
      }
      if(!this.pattern.test(this.phone)){
        Toast({
          message: '',
          position: 'top',
        })
        return
      }
      if(this.uid==''){
        this.uid = Date.parse(new Date())
      }
      
      axios.get(`https://web.rongxinvip.com/weixin/getAppSign?code=${this.uid}`)
      .then(res=>{
        if(res.data.code===0){
          this.sign = res.data.data
          const instance = axios.create({
            headers: {'requestKey': this.sign}
          });
          instance.post('https://web.rongxinvip.com/weixin/sms/sendSmsCode',{
            phone:this.phone
          })
          .then(res=>{
            console.log('postRes',res)
            Toast({
              message: res.data.msg,
              position: 'top',
            })
          })
          .catch(err=>{
            console.log('postRrr',err)
          })
        }
        console.log('res',res)
      })
      .catch(err=>{
        console.log(err)
      })
      
      
    }
  }
}
</script>

