<template>
  <div>
    <van-loading v-if="loadFlag" class="loading" size="48px">......</van-loading>
    <div v-if="!loadFlag" class="AssetInfo">
      <div class="title"></div>
      <van-form @submit="onSubmit">
        <van-field
          v-model="antPoints"
          label-width="85px"
          required
          type="number"
          name="antPoints"
          label=""
          placeholder=""
          :rules="[{ required: true, message: '' },{ validator, message: ''}]"
        />
        <van-field
          v-model="socialSecurity"
          type="number"
          label-width="40px"
          name="socialSecurity"
          label=""
          placeholder=""
          :rules="[{ validator:validatorMonth, message: '120'}]"
        />
        <van-field
          v-model="insurancePolicy"
          type="number"
          label-width="40px"
          name="insurancePolicy"
          label=""
          placeholder=""
          :rules="[{ validator:validatorYear, message: '30'}]"
        />
        <van-field
          v-model="estateValue"
          type="number"
          label-width="40px"
          name="estateValue"
          label=""
          placeholder=""
          :rules="[{ validator:validatorHouse, message: '100020'}]"
        />
        <van-field
          v-model="carValue"
          type="number"
          label-width="40px"
          name="carValue"
          label=""
          placeholder=""
          :rules="[{ validator:validatorCar, message: '100'}]"
        /><van-field
          v-model="annualIncome"
          type="number"
          label-width="70px"
          name="annualIncome"
          label=""
          placeholder=""
          :rules="[{ validator:validatorPerson, message: '100'}]"
        />
        <van-field name="salaryPayment" label="" label-width="70px">
          <template #input>
            <van-radio-group v-model="salaryPayment" direction="horizontal">
              <van-radio name="0"></van-radio>
              <van-radio name="1"></van-radio>
            </van-radio-group>
          </template>
        </van-field>
        <van-field name="creditHistory" label="" label-width="70px">
          <template #input>
            <van-radio-group v-model="creditHistory">
              <van-radio name="0"></van-radio>
              <van-radio name="1">90</van-radio>
              <van-radio name="2">90</van-radio>
            </van-radio-group>
          </template>
        </van-field>
        <div style="margin: 26px 16px 16px 16px;">
          <van-button round block type="info" native-type="submit"> </van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>
<script>
import { Button,RadioGroup, Radio, Toast, Loading } from "vant";
import axios from 'axios'
import { setTimeout } from 'timers';

export default {
  name: 'AssetInfo',
  components: {
    vanButton: Button,
    vanRadioGroup:RadioGroup,
    vanRadio:Radio,
    vanLoading:Loading,
  },
  data() {
    return {
        loadFlag:false,
        antPoints: '',
        socialSecurity:'',
        insurancePolicy:'',
        estateValue:'',
        carValue:'',
        annualIncome:'',
        salaryPayment:'0',
        creditHistory:'0'
    }
  },
  mounted(){
  },
  methods:{
    validator(val) {
      return val>350 && val<950;
    },
    validatorMonth(val){
      return val<=120;
    },
    validatorYear(val){
      return val<=30;
    },
    validatorHouse(val){
      return (val>=20 && val<=1000)||val===''
    },
    validatorCar(val){
      return (val>=1 && val<100)||val===''
    },
    validatorPerson(val){
      return (val>=1 && val<100)||val===''
    },
    onSubmit(values) {
      console.log('submit', values);
      let obj ={
        ...values,
        ...this.$route.query
      }
      if(!this.$route.query.employeeNumber||this.$route.query.employeeNumber<1000||this.$route.query.employeeNumber>99999){
        delete obj.employeeNumber
      }
      if(obj.estateValue==''){
        delete obj.estateValue
      }else{
        obj.realEstate='1'
      }
      if(obj.carValue==''){
        delete obj.carValue
      }else{
        obj.car='1'
      }
      const formData = new FormData()
      for(let key in obj){
        formData.append(key, obj[key]);
      }
      const instance = axios.create(
        {
        headers: {'requestKey': this.$route.query.sign}
        }
      );
      // instance.post('https://web.rongxinvip.com/weixin/sms/checkSmsCode',{
      //   phone:this.$route.query.phone,
      //   smsCode:this.$route.query.smsCode
      // }
      // ).then(function (response) {
      //   Toast({
      //         message: response.data.msg,
      //         position: 'top',
      //       })
      //     console.log('response',response);
      //     if(response.data.code===500){
      //       return
      //     }
      //     obj.verifyCode=obj.smsCode
          delete obj.smsCode
          delete obj.sign
          instance.post('https://web.rongxinvip.com/weixin/add',obj)
          .then(res=>{
            if(res.data.code===0&&res.data.data!==0){//
              this.loadFlag=true
              setTimeout(res=>{
                this.loadFlag=false
                console.log('res',res)
                this.$router.push({
                  path:'/Result',
                  query:{
                    ...this.$route.query,
                    success:'1'
                  }
                })
              },5000)
            }else if(res.data.code===0&&res.data.data===0){//
              this.loadFlag=true
                setTimeout(res=>{
                  this.loadFlag=false
                  console.log('res',res)
                  this.$router.push({
                    path:'/Result',
                    query:{
                      ...this.$route.query,
                      success:'2'
                    }
                  })
                },5000)
              
            }else{//
              Toast({
                message: res.data.msg,
                position: 'center',
              })
            }
          })
          .catch(err=>{
            console.log('err',err)
          })
      // }).catch(function (error) {
      //     console.log('error',error);
      // });
    },
    toSelect() {
        this.show = true;
    },
    confirmFn(value) {
        this.show = false;
        let str =''
        value.forEach(item=>{
          str += item.name+'/'
        })
        this.city = str.slice(0,-1)
        console.log(value)
    },
    cancelFn() {
        this.show = false;
    }
  }
}
</script>

